import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"

import styles from "./dream-work.module.scss"
import PageContentSeparator from "../../components/page-content-separator"
import ParagraphSeparator from "../../components/paragraph-separator"
import dreamWorkImage1 from "../../images/dreamWorkImage1.png"
import dreamWorkImage2 from "../../images/dreamWorkImage2.png"
import dreamWorkImage3 from "../../images/dreamWorkImage3.png"
import dreamWorkFontsImage from "../../images/dreamWorkFonts.png"

export default () => {
  const { mockupDreamWork } = useStaticQuery(graphql`
    query {
      mockupDreamWork: file(relativePath: { eq: "mockupDreamWork.png" }) {
        childImageSharp {
          fixed(height: 100) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <React.Fragment>
      <div className={styles.pageHeadingSection}>
        <Img
          fixed={mockupDreamWork.childImageSharp.fixed}
          objectFit="cover"
          objectPosition="50% 50%"
          alt=""
          className={styles.pageHeadingImage}
        />
      </div>
      <div className={styles.pageMainContentSection}>
        <div className={styles.pageMainContent}>
          <PageContentSeparator />
          <div>
            <div className={styles.projectTitle}>DreamWork</div>
            <div className={styles.projectSubTitle}>
              Career Information Website
            </div>
          </div>
          <ParagraphSeparator />
          <div className={styles.projectText}>
            <div>
              I create a website for posting and seeking jobs in the design
              field. The main color is used based on the 2020 color trend:
              classic blue. It certainly brings a feeling of trust and
              gentleness for the users who are job seekers and recruiters.
              Tetragons, pentagons, and hexagons are used throughout the website
              to indicate the simple and straightforward working style for the
              company.
            </div>
            <ParagraphSeparator />
            <div className={styles.projectDetailsRow}>
              <div className={styles.projectDetailsLabel}>Main color:</div>
              <div>
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.colorBox1}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.colorBox2}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.colorBox3}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.colorBox4}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.colorBox5}`}
                />
              </div>
            </div>
            <ParagraphSeparator />
            <div className={styles.projectDetailsRow}>
              <div className={styles.projectDetailsLabel}>Fonts:</div>
              <img src={dreamWorkFontsImage} alt="" />
            </div>
            <ParagraphSeparator />
            <div>Full page:</div>
            <ParagraphSeparator />
          </div>
        </div>
      </div>
      <div className={styles.projectImageContainer}>
        <img src={dreamWorkImage1} className={styles.projectImage} alt="" />
        <PageContentSeparator />
        <img src={dreamWorkImage2} className={styles.projectImage} alt="" />
        <PageContentSeparator />
        <img src={dreamWorkImage3} className={styles.projectImage} alt="" />
      </div>
      <PageContentSeparator />
    </React.Fragment>
  )
}
